import moment from 'moment';

/**
 * Model States
 */
const modelStates = {
  dirty: 'dirty', // Model has changes that haven't been persisted to server
  persisting: 'persisting', // Model is dirty, and persiting changes to server
  pristine: 'pristine', // Model is in sync with the server
};

const teamModel = {
  type: 'team',
  name: '',
  neo4jConnection: '',
  neo4jAuth: '',
  imageURL: '',
  reportCount: 0,
  integrations: [],
  twitterCredentials: []
};

/**
 * Creates a new team model
 */
const buildNewTeamModel = () => {
  return teamModel;
};

/**
 * Creates an existing team model
 */

 const buildTeamModel = data => {
   const createdDate = new Date(data.createdDate);
   const lastActivityDate = data.lastActivityDate ? new Date(data.lastActivityDate) : undefined;
   const formattedLastActivityDate = lastActivityDate ? moment(lastActivityDate).format('MMMM Do YYYY') : 'never';
   const formattedCreatedDate = moment(createdDate).format('MMMM Do, YYYY');

   const { name, downloadCount, id, imageURL, userCount, reports, twitterCredentials, integrations } = data;

   const dataProps = {
     name,
     id,
     downloadCount,
     imageURL,
     userCount,
     reports,
     createdDate,
     formattedCreatedDate,
     lastActivityDate,
     formattedLastActivityDate,
     twitterCredentials,
     integrations
   };

   return {
     ...teamModel,
     state: modelStates.pristine,
     ...dataProps
   };
 };

const getSaveProps = model => {
  return {
    name: model.name,
    neo4jConnection: model.neo4jConnection,
    neo4jAuth: model.neo4jAuth,
    imageURL: model.imageURL
  };
};

/**
 * returns whether or not the model is valid
 */
const isModelValid = model => {
  if (!model) return;
  if (!model.get('name') || model.get('name').trim() < 3) {
    return false;
  }
  if (!model.get('neo4jConnection').trim() || !model.get('neo4jAuth').trim()) {
    return false;
  }
  return true;
};

const buildTeamCollection = teams => {
  if (!teams) {
    throw new Error('missing teams');
  }
  
  return teams
    .map(buildTeamModel)
    .reduce((acc, cur) => {
      acc[cur.id] = cur;
      return acc;
    }, {});
};

const buildTeamList = (teamCollection={}) => {
  return Object.keys(teamCollection)
    .map(cur => teamCollection[cur])
    .sort((a, b) => a.name - b.name);
};

export {
  isModelValid,
  buildNewTeamModel,
  getSaveProps,
  buildTeamModel,
  buildTeamCollection,
  buildTeamList
};



