import callApi from '../api/call';
import { downloadCSV } from './downloadService';

function getTeamList() {
  return callApi('teams', 'get')
    .catch(error => {
      return { error };
    });
}

function getTeamProfileDetails(name) {
  const opts = {
    params: { name }
  };
  return callApi('teamprofile', 'get', opts)
    .catch(error => {
      return { error };
    });
}

function saveTwitterCredential(twitterCredentialModel) {
  const params = {
    body: {
      twitterCredentialModel
    }
  };
  return callApi('teamprofile/twittercredential', 'post', params);
}

function createIntegration(teamId, type) {
  const params = {
    body: {
      teamId,
      type
    }
  };
  return callApi('teamprofile/integration', 'post', params);
}

function deleteIntegration(id) {
  const params = {
    body: {
      id
    }
  };
  return callApi('teamprofile/integration', 'delete', params);
}

function downloadIntegrationReports(id) {
  const mediaReportOpts = {
    params: {
      id,
      reportName: 'mediaId'
    }
  };
  const mediaReportPromise = callApi('teamprofile/integration/download', 'get', mediaReportOpts)
    .then(resp => {
      downloadCSV(resp.results, 'mediaId_report');
    });
    
  const friendsAndFollowersReportOpts = {
    params: {
      id,
      reportName: 'friendsAndFollowers'
    }
  };
  const friendsAndFollowersReportPromise = callApi('teamprofile/integration/download', 'get', friendsAndFollowersReportOpts)
    .then(resp => {
      debugger;
      downloadCSV(resp.results, 'friends_followers_report');
    });
  return Promise.all([mediaReportPromise, friendsAndFollowersReportPromise]);
}

export {
  getTeamList,
  getTeamProfileDetails,
  saveTwitterCredential,
  createIntegration,
  deleteIntegration,
  downloadIntegrationReports
};



