import { call, put, takeLatest } from 'redux-saga/effects';
import { AUTH_SIGN_IN_REQUEST, signInError, signInReceive } from '../store/actions/auth';
import { authenticateUser } from '../services/authService';

function *signInRequest(action={}) {
  const authResp = yield call(authenticateUser, action.username, action.password);
  if (authResp.error) {
    yield put(signInError(authResp.error));
  }
  
  const { token, user, expiresIn } = authResp;
  const expiresDate = new Date();
  expiresDate.setHours(expiresDate.getHours() + (expiresIn / 60 / 60));
  
  const userSession = {
    tokenExpirationDate: expiresDate,
    userData: user,
    token: token
  };
  yield(put(signInReceive(userSession)));
}

function *authSaga() {
  yield takeLatest(AUTH_SIGN_IN_REQUEST, signInRequest);
}

export default authSaga;



