import React from 'react';
import SecondaryButton from '../forms/buttons/secondaryButton';
import PrimaryButton from '../forms/buttons/primaryButton';

const twitterCredentialsForm = ({twitterCredentialModel, onSave, onCancel}) => {
  const editedModel = {
    ...twitterCredentialModel,
    state: twitterCredentialModel.state === 'new' ? 'new' : 'dirty'
  };
  
  const saveHandler = () => {
    const { consumerKey, consumerSecret, accessTokenKey, accessTokenSecret } = editedModel;
    if (!consumerKey || !consumerSecret || !accessTokenKey || !accessTokenSecret) {
      return;
    }
    onSave(editedModel);
  };
  
  const changeHandler = (propName, val) => {
    editedModel[propName] = val;
  };
  
  return (
    <div className="twitterCredential__form">
      <div className="twitterCredential__form--container">
        
        <div className="twitterCredential__form__editField">
          <label
            htmlFor="twitterProfile-input-consumer-key"
            className="twitterCredential__form__editField--label"
          >
            Consumer Key
          </label>
          <input
            id="twitterProfile-input-consumer-key"
            type="text"
            className="twitterCredential__form__editField--input"
            defaultValue={twitterCredentialModel.consumerKey}
            onChange={e => changeHandler('consumerKey', e.target.value)}
            />
        </div>
        
        <div className="twitterCredential__form__editField">
          <label
            htmlFor="twitterProfile-input-consumer-secret"
            className="twitterCredential__form__editField--label"
          >Consumer Secret</label>
          <input
            id="twitterProfile-input-consumer-secret"
            type="text"
            className="twitterCredential__form__editField--input"
            defaultValue={twitterCredentialModel.consumerSecret}
            onChange={e => changeHandler('consumerSecret', e.target.value)}
            />
        </div>
        
        <div className="twitterCredential__form__editField">
          <label
            htmlFor="twitterProfile-input-consumer-access-token-key"
            className="twitterCredential__form__editField--label"
          >Access Token Key</label>
          <input
            id="twitterProfile-input-consumer-access-token-key"
            type="text"
            className="twitterCredential__form__editField--input"
            defaultValue={twitterCredentialModel.accessTokenKey}
            onChange={e => changeHandler('accessTokenKey', e.target.value)}
            />
        </div>
        
        <div className="twitterCredential__form__editField">
          <label
            htmlFor="twitterProfile-input-consumer-access-token-secret"
            className="twitterCredential__form__editField--label"
          >Access Token Secret</label>
          <input
            id="twitterProfile-input-consumer-access-token-secret"
            type="text"
            className="twitterCredential__form__editField--input"
            defaultValue={twitterCredentialModel.accessTokenSecret}
            onChange={e => changeHandler('accessTokenSecret', e.target.value)}
            />
        </div>
      </div>
      
      <div className="twitterCredential__readOnly__actions">
        <div className="twitterCredential__actions">
          <SecondaryButton text="Cancel" clickHandler={onCancel}/>
          <PrimaryButton text="Save" clickHandler={saveHandler}/>
        </div>
      </div>
      
    </div>
  );
};

export default twitterCredentialsForm;



