import {
   buildTeamCollection,
   buildTeamList
} from '../../factories/teamFactory';
import {
  TEAMS_RECEIVE,
  TEAMS_REQUEST,
  TEAMS_RESET,
} from '../actions/teams';

const teams = {
  collection: {},
  list: [],
};

const defaultState = {
  ...teams
};

export {
  defaultState
};

export default function reducer(state = defaultState, action) {
  switch(action.type) {
    case TEAMS_RECEIVE:
      const collection = buildTeamCollection(action.teams);
      return {
        ...state,
        collection,
        list: buildTeamList(collection),
        isLoading: false
      };
    case TEAMS_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case TEAMS_RESET:
      return {
        ...defaultState
      };
    default:
      return state;
  }
}



