import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FilterDropdown from '../filterDropdown/filterDropdown';
import PrimaryButton from '../forms/buttons/primaryButton';
import IntegrationRow from './integrationRow';
import './integrations.css';

class Integrations extends Component {
  constructor(props) {
    super(props);
    this.state = {
      integrationType: 'Twitter'
    };
  }
  
  handleIntegrationTypeSelect(type) {
    this.setState(() => {
      return {
        integrationType: type
      };
    });
  }
  
  handleStartIntegration() {
    this.props.onStartIntegration(this.state.integrationType.toLowerCase());
  }
  
  renderIntegrations() {
    return this.props.integrations.map(cur => {
      return (
        <IntegrationRow
          integrationModel={cur}
          key={cur.type+cur.createdDate+cur.status}
          handleDelete={this.props.onDeleteIntegration}
          handleDownload={this.props.onIntegrationReport}
        />
      );
    });
  }
  
  render() {
    return (
      <div className="integrations">
        <div className="integrations__start">
          <div className="integrations__start__title">
            <span className="integrations__start__title--font">
              Start New Integration
            </span>
          </div>
          
          <div className="integrations__start__actions">
            <FilterDropdown
              selectedItem={this.state.integrationType}
              items={['Twitter']}
              selectHandler={this.handleIntegrationTypeSelect.bind(this)} />
              
            <PrimaryButton
              text="Start"
              clickHandler={this.handleStartIntegration.bind(this)}
              />
          </div>
          
          <div className="integrations__list__container">
            <div className="integrations__list">
              {this.props.integrations.length === 0 &&
                (
                  <span className="integrations__list__noResults">
                    No existing Integrations
                  </span>
                )
              }
              { this.renderIntegrations() }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Integrations.propTypes = {
  integrations: PropTypes.array.isRequired,
  onStartIntegration: PropTypes.func.isRequired,
  onDeleteIntegration: PropTypes.func.isRequired
};

export default Integrations;



