import React from 'react';
import './progressBar.css';

const getWidthStyle = percent => {
  return {
    width: `${percent*100 || 0}%`
  };
};

const progressBar = ({percent, label}) => {
  return (
    <div className="progressBar">
      <div className="progressBar__meter">
        <div className="progressBar__meter__progress" style={getWidthStyle(percent)}></div>
        <div className="progressBar__meter__label">{label}</div>
      </div>
    </div>
  );
};


export default progressBar;



