import React, { Component } from 'react';
import './buttonRipple.css';

class ButtonRipple extends Component {
  setRef(el) {
    if (this.refEl) {
      return;
    }
    this.refEl = el;
  }
  
  handleMouseDownEvent(e) {
    // Require the ref element
    if (!this.refEl) {
      return;
    }

    // Left clicks only
    if (e.type !== 'mousedown' || e.button !== 0) {
      return;
    }
    
    const buttonEl = this.refEl.parentElement;
    
    // Disallow clicks when button is disabled
    if (!buttonEl || buttonEl.disabled) {
      return;
    }
    
    if (!this._rippleEl) {
      const rippleEl = document.createElement('div');
      rippleEl.classList.add('buttonRipple__ripple');
      this.refEl.appendChild(rippleEl);
      this._rippleEl = this.refEl.children[0];
    }

    const btnRect = buttonEl.getBoundingClientRect();
    const radius = Math.sqrt(btnRect.height * btnRect.height + btnRect.width * btnRect.width);
    const diameter = radius * 2 + 'px';
    
    // Set ripple element position and diameter
    this._rippleEl.style.width = diameter;
    this._rippleEl.style.height = diameter;
    this._rippleEl.style.top = Math.round(e.pageY - btnRect.top - radius) + 'px';
    this._rippleEl.style.left = Math.round(e.pageX - btnRect.left - radius) + 'px';
    
    // <div className="buttonRipple__ripple buttonRipple__ripple--animating"></div>
    this._rippleEl.classList.remove('buttonRipple__ripple--animating');
    this._rippleEl.classList.add('buttonRipple__ripple--visible');
    
    // Start the animation
    requestAnimationFrame(() => this._rippleEl.classList.add('buttonRipple__ripple--animating'));
  }
  
  handleMouseUpEvent(e) {
    if (!this._rippleEl) {
      return;
    }
    // allow a repaint to occur before removing class so animation shows for
    requestAnimationFrame(() => this._rippleEl.classList.remove('buttonRipple__ripple--visible'));
  }
  
  render() {
    return (
      <div
        className="buttonRipple__container"
        ref={el => this.setRef(el)}
        onMouseDown={this.handleMouseDownEvent.bind(this)}
        onMouseUp={this.handleMouseUpEvent.bind(this)}
        onMouseLeave={this.handleMouseUpEvent.bind(this)}
        >
      </div>
    );
  }
}
export default ButtonRipple;



