import {
  makeNewTwitterCredentialModel,
  makeTwitterCredentialModel
} from '../../factories/twitterCredentialFactory';
import {
  makeIntegrationModel,
  makeNewIntegrationModel
} from '../../factories/integrationFactory';

import {
  TEAM_PROFILE_REQUEST,
  TEAM_PROFILE_RECEIVE,
  TEAM_PROFILE_RESET,
  TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST,
  TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_RECEIVE,
  TEAM_PROFILE_NEW_INTEGRATION_RECEIVE,
  TEAM_PROFILE_NEW_INTEGRATION_REQUEST,
  TEAM_PROFILE_REQUEST_DELETE_INTEGRATION
} from '../actions/teamProfile';

const teamProfile = {
  integrations: [],
  twitterCredential: {},
  name: '',
  id: undefined,
  status: 'not-loaded' // not-loaded, loading, loaded
};

const defaultState = {
  ...teamProfile
};

export {
  defaultState
};

export default function reducer(state = defaultState, action) {
  switch(action.type) {
    case TEAM_PROFILE_NEW_INTEGRATION_REQUEST:
      return {
        ...state,
        integrations: state.integrations.concat([makeNewIntegrationModel()])
      };
    case TEAM_PROFILE_NEW_INTEGRATION_RECEIVE:
      return {
        ...state,
        integrations: state.integrations.map(cur => {
          if (cur.id !== -1 || cur.type !== action.integrationModel.type) {
            return cur;
          }
          return {
            ...cur,
            ...makeIntegrationModel(action.integrationModel)
          };
        })
      };
    case TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST:
    case TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_RECEIVE:
      return {
        ...state,
        twitterCredential: Object.assign({}, action.twitterCredentialModel)
      };
    case TEAM_PROFILE_REQUEST:
      return {
        ...state,
        status: 'loading'
      };
    case TEAM_PROFILE_RESET:
      return {
        ...defaultState
      };
    case TEAM_PROFILE_RECEIVE:
      const {
        twitterCredential,
        name,
        id
      } = action.teamProfile;
        
      const integrations = action.teamProfile.integrations.map(cur => makeIntegrationModel(cur));
      
      const twitterCredentialModel = twitterCredential ?
        makeTwitterCredentialModel(twitterCredential) :
        makeNewTwitterCredentialModel({teamId: action.teamProfile.id});
      return {
        ...state,
        integrations,
        twitterCredential: twitterCredentialModel,
        id,
        name,
        status: 'loaded'
      };
    case TEAM_PROFILE_REQUEST_DELETE_INTEGRATION:
      return {
        ...state,
        integrations: state.integrations.filter(cur => cur.id !== action.id)
      };
    default:
      return state;
  }
}



