import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  requestTeamProfile,
  requestSaveTwitterCredential,
  requestStartIntegration,
  requestDeleteIntegration,
  requestIntegrationReport
} from '../../store/actions/teamProfile';
import {
  withRouter,
  matchPath,
  NavLink
} from 'react-router-dom';
import TwitterCredentials from '../../components/twitterCredentials/twitterCredentials';
import Integrations from '../../components/integrations/integrations';
import './teamProfile.css';

const routeMap = {
  'twitterCredentials': '/admin/team/:name/twittercredentials',
  'integrations': '/admin/team/:name'
};

class TeamProfile extends Component {
  componentWillMount() {
    this.props.getTeamProfile(this.props.match.params.name);
  }
  
  componentWillUnmount() {
    
  }
  
  isRouteActive(name) {
    return matchPath(this.props.location.pathname, {
      path: routeMap[name],
      exact: true,
      strict: false
    });
  }
  
  renderTabs() {
    if (this.props.teamProfile.status !== 'loaded') {
      return;
    }
    return (
      <div className="teamProfile__tabs__container">
        <NavLink exact to={`/admin/team/${this.props.teamProfile.name}`} activeClassName="teamProfile__tab__item--active" className="teamProfile__tab__item">
          <span className="teamProfile__tab__item--text">integrations</span>
        </NavLink>
        
        <NavLink exact to={`/admin/team/${this.props.teamProfile.name}/twittercredentials`} activeClassName="teamProfile__tab__item--active" className="teamProfile__tab__item">
          <span className="teamProfile__tab__item--text">twitter credentials</span>
        </NavLink>
      </div>
    );
  }
  
  renderContent() {
    if (this.props.teamProfile.status !== 'loaded') {
      return;
    }

    if (this.isRouteActive('integrations')) {
      return (
        <Integrations
          integrations={this.props.teamProfile.integrations}
          onStartIntegration={this.startIntegration.bind(this)}
          onDeleteIntegration={this.deleteIntegration}
          onIntegrationReport={this.downloadIntegrationReport}
          />
      );
    } else if (this.isRouteActive('twitterCredentials')) {
      return (
        <TwitterCredentials
          model={this.props.teamProfile.twitterCredential}
          saveHandler={this.saveTwitterCredential.bind(this)}
          />
        );
    }
  }
  
  saveTwitterCredential(model) {
    if (!model) {
      return;
    }
    this.props.saveTwitterCredential(model);
  }
  
  startIntegration(type) {
    if (!type) {
      return;
    }
    this.props.startIntegration(this.props.teamProfile.id, type);
  }
  
  deleteIntegration = id => {
    this.props.deleteIntegration(id);
  }
  
  downloadIntegrationReport = id => {
    this.props.downloadIntegrationReport(id);
  }
  
  render() {
    return (
      <div className="teamProfile__board">
        <div className="teamProfile__board__container">
          <div className="teamProfile__label">
            <span className="teamProfile__label--font">{this.props.teamProfile.name}</span>
          </div>
          {this.renderTabs()}
          <div className="teamProfile__content">
            {this.renderContent()}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    teamProfile: state.teamProfile
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getTeamProfile: name => dispatch(requestTeamProfile(name)),
    saveTwitterCredential: model => dispatch(requestSaveTwitterCredential(model)),
    startIntegration: (teamId, type) => dispatch(requestStartIntegration(teamId, type)),
    deleteIntegration: id => dispatch(requestDeleteIntegration(id)),
    downloadIntegrationReport: (id, reportName) => dispatch(requestIntegrationReport(id))
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TeamProfile));



