import React from 'react';
import PropTypes from 'prop-types';
import FolderIcon from '../../icons/folderIcon';
import UpdateIcon from '../../icons/updateIcon';
import DownloadIcon from '../../icons/downloadIcon';
import UserIcon from '../../icons/userIcon';
import { Link } from 'react-router-dom';
import './teamCard.css';

const teamCard = ({team}) => {
  return (
    <div className="teamCard" key={team.name}>
      
      <div className="teamCard__header">
        <div className="teamCard__header__title">
          <span className="teamCard__header__title--name">{team.name}</span>
          <span className="teamCard__header__title--date">created on&nbsp;{team.formattedCreatedDate}</span>
        </div>
      </div>
      
      <div className="teamCard__stats">
        <div className="teamCard__stat__row">
          <div className="teamCard__stat__item">
            <div className="teamCard__stat__item__top">
              <div className="teamCard__stat__item__top__data">
                {team.userCount || 0}
              </div>
            </div>
            <div className="teamCard__stat__item__label">
              <div className="teamCard__stat__item__label--icon">
                <UserIcon />
              </div>
              <span>
                Total Users
              </span>
            </div>
          </div>
          
          <span className="teamCard__stat__item__divider"></span>
          
          <div className="teamCard__stat__item">
            <div className="teamCard__stat__item__top">
              <div className="teamCard__stat__item__top__data">
                {team.reports.length || 0}
              </div>
            </div>
            <div className="teamCard__stat__item__label">
              <div className="teamCard__stat__item__label--icon">
                <FolderIcon />
              </div>
              <span>
                Total Reports
              </span>
            </div>
          </div>
        </div>
        <div className="teamCard__stat__row">
          <div className="teamCard__stat__item">
            <div className="teamCard__stat__item__top">
              <div className="teamCard__stat__item__top__data">
                {team.formattedLastActivityDate}
              </div>
            </div>
            <div className="teamCard__stat__item__label">
              <div className="teamCard__stat__item__label--icon">
                <UpdateIcon />
              </div>
              <span>
                Last Activity
              </span>
            </div>
          </div>
        
        <span className="teamCard__stat__item__divider"></span>
        
        <div className="teamCard__stat__item">
          <div className="teamCard__stat__item__top">
            <div className="teamCard__stat__item__top__data">
              {team.downloadCount || 0}
            </div>
          </div>
          <div className="teamCard__stat__item__label">
            <div className="teamCard__stat__item__label--icon">
              <DownloadIcon />
            </div>
            <span>
              Download Count
            </span>
          </div>
        </div>
        </div>
      </div>
      <div className="teamCard__actions">
        <Link to={`/admin/team/${team.name}`} className="teamCard__actions__item--button--text">
          View Team Profile
        </Link>
      </div>
    </div>
  );
};

teamCard.propTypes = {
  team: PropTypes.object.isRequired
};

export default teamCard;



