import React, { Component } from 'react';
import AppBarSettings from './appBarSettings';
import { withRouter } from 'react-router-dom';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { connect } from  'react-redux';
import AppBarSettingsMenu from './appBarSettingsMenu';
import './appBar.css';

class AppBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSettingsMenuOpen: false
    };
  }
  
  renderSettingsDropdown() {
    return (
      <CSSTransitionGroup
        transitionName="flyIn"
        transitionEnterTimeout={200}
        transitionLeaveTimeout={200}>
        {this.state.isSettingsMenuOpen &&
          <AppBarSettingsMenu closeHandler={() => this.toggleSettingsMenu() } />
        }
      </CSSTransitionGroup>
    );
  }
  
  toggleSettingsMenu() {
    this.setState(() => ({isSettingsMenuOpen: !this.state.isSettingsMenuOpen}));
  }
  
  render() {
    return (
      <div className="appBar">
        {this.renderSettingsDropdown()}
        <AppBarSettings userName={this.props.userName} toggleSettingsMenu={() => this.toggleSettingsMenu()}/>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const user = state.auth.user;
  return {
    userName: `${user.firstName} ${user.lastName}`,
    isAdmin: user.role === 'admin'
  };
};

export default withRouter(connect(mapStateToProps)(AppBar));



