import React from 'react';
import TwitterIcon from '../../icons/twitterIcon.js';
import InfiniteSpinner from '../spinners/infiniteSpinner';
import PrimaryButton from '../forms/buttons/primaryButton';
import SecondaryButton from '../forms/buttons/secondaryButton';
import ProgressBar from '../progressBar/progressBar';
import './integrationRow.css';

const integrationRow = ({integrationModel, handleDelete, handleDownload}) => {
  const renderMsg = () => {
    switch(integrationModel.status) {
      case 'completed':
        return `${integrationModel.type} integration successfully completed on ${integrationModel.formattedFinishedDate}`;
      case 'error':
        return `${integrationModel.type} integration finished with error "${integrationModel.statusMsg} on ${integrationModel.formattedFinishedDate}`;
      case 'pending':
      case 'cancelling':
        return integrationModel.statusMsg;
      case 'inProgress':
        return `${integrationModel.type} integration running`;
      case 'cancelled':
        return `${integrationModel.type} integration cancelled on ${integrationModel.formattedFinishedDate}`;
      default:
        return integrationModel.statusMsg;
    }
  };
  
  const renderProgressBar = () => {
    if (integrationModel.status !== 'inProgress') {
      return;
    }
    const completedCount = integrationModel.completedCount;
    const totalCount = integrationModel.totalCount;
    const percentCompleted = completedCount / totalCount;
    if (isNaN(percentCompleted)) {
      return;
    }
    
    const label = `${completedCount.toString()} / ${totalCount.toString()}`;
    
    return (
      <div className="integration__row__progressBar">
        <ProgressBar
          percent={percentCompleted}
          label={label}
          />
      </div>
    );
  };
  
  const renderStatusMsg = () => {
    return (
      <div className="integration__row__status__msg">
        <span className="integration__row__status__msg--font">
          { renderMsg() }
        </span>
      </div>
    );
  };
  
  const renderUserInProgress = () => {
    if (integrationModel.status !== 'inProgress') {
      return;
    }
    
    const userInProgress = integrationModel.userInProgress;
    if (!userInProgress || !userInProgress.name) {
      return;
    }
    
    return (
      <div className="integration__row__userInProgress">
        <span className="integration__row__userInProgress__label">In Progress</span>
        <span className="integration__row__userInProgress__divider">-</span>
        <div className="integration__row__userInProgress__details">
          <span className="integration__row__userInProgress__details__name">{userInProgress.name}</span>
          <span className="integration__row__userInProgress__details__subName">(@{userInProgress.twitterID})</span>
        </div>
      </div>
    );
  };
  
  return (
    <div className="integration__row">
      <div className="integration__row__description">
        <div className="integration__row__description--icon">
          <TwitterIcon />
        </div>
        
        <span className="integration__row__description--text">
          {integrationModel.type} Integration
        </span>
        
        <div className="integration__row__description__meta">
          <div className="integration__row__description__meta__item">
            <span className="integration__row__description__meta--key">
              Started
            </span>
            <span className="integration__row__description__meta--value">
              {integrationModel.formattedCreatedDate}
            </span>
            <span className="integration__row__description__meta--key">
              by
            </span>
            <span className="integration__row__description__meta--value">
              {integrationModel.createdByName}
            </span>
          </div>
        </div>
        {integrationModel.status === 'completed' &&
          (
            <div className="integration__row__description__meta">
              <div className="integration__row__description__meta__item">
                <span className="integration__row__description__meta--key">
                  Finished
                </span>
                <span className="integration__row__description__meta--value">
                  {integrationModel.formattedFinishedDate}
                </span>
              </div>
            </div>
          )
        }
      </div>
      
      <div className="integration__row__status">
        {renderStatusMsg()}
        {renderUserInProgress()}
        {renderProgressBar()}
        
        <div className="integration__row__status__indicator">
          {integrationModel.status === 'pending' &&
            (
              <div className="integration__row__status__indicator--spinner">
                <InfiniteSpinner />
              </div>
            )
          }
          
        </div>
      </div>
      
      <div className="integration__row__actions">
        <PrimaryButton
          text="Download"
          clickHandler={() => handleDownload(integrationModel.id)}/>
      </div>
    </div>
  );
};

export default integrationRow;



