import { makeActionCreator } from './index';

export const AUTH_SIGN_OUT             = 'AUTH_SIGN_OUT';
export const AUTH_HYDRATE_USER_SESSION = 'AUTH_HYDRATE_USER_SESSION';
export const AUTH_SIGN_IN_REQUEST      = 'AUTH_SIGN_IN_REQUEST';
export const AUTH_SIGN_IN_RECEIVE      = 'AUTH_SIGN_IN_RECEIVE';
export const AUTH_SIGN_IN_ERROR        = 'AUTH_SIGN_IN_ERROR';
export const AUTH_SET_AUTHENTICATED    = 'AUTH_SUCCESS';

export const hydrateUserSession = makeActionCreator(AUTH_HYDRATE_USER_SESSION);
export const signOut            = makeActionCreator(AUTH_SIGN_OUT);
export const signInRequest      = makeActionCreator(AUTH_SIGN_IN_REQUEST, 'username', 'password');
export const signInReceive      = makeActionCreator(AUTH_SIGN_IN_RECEIVE, 'userSession');
export const signInError        = makeActionCreator(AUTH_SIGN_IN_ERROR, 'error');
export const setAuthenticated   = makeActionCreator(AUTH_SET_AUTHENTICATED);



