import { buildUserModel } from '../../factories/userFactory';

import { isUserSessionActive } from '../../services/authService';

import {
  AUTH_SIGN_IN_REQUEST,
  AUTH_SIGN_IN_RECEIVE,
  AUTH_SIGN_IN_ERROR,
  AUTH_SET_AUTHENTICATED,
  AUTH_SIGN_OUT,
  AUTH_HYDRATE_USER_SESSION
} from '../actions/auth';

const auth = {
  user: undefined,
  token: undefined,
  tokenExpirationDate: undefined,
  status: 'default',  // default, authenticating, error, authenticated
  msg: '' // error messages, etc...
};

const defaultState = {
  ...auth
};

export {
  defaultState
};

/*
 * Helper functions
 */
const setUserSession = (state, userSession) => {
  if (!userSession) {
    return {
      ...state
    };
  }
  const { userData, tokenExpirationDate, token } = userSession;
  return {
    ...state,
    user: buildUserModel(userData),
    tokenExpirationDate,
    token
  };
};

export default function reducer(state = defaultState, action) {
  switch(action.type) {
    case AUTH_SIGN_IN_RECEIVE:
      return setUserSession(state, action.userSession);
    case AUTH_SIGN_IN_ERROR:
      return {
        ...state,
        status: 'error',
        msg: action.error.message
      };
    case AUTH_SIGN_IN_REQUEST:
      return {
        ...state,
        status: 'authenticating',
        msg: ''
      };
    case AUTH_HYDRATE_USER_SESSION:
      if (isUserSessionActive(action.sessionData)) {
        return {
          ...setUserSession(state, action.sessionData),
          status: 'authenticated'
        };
      } else {
        return {
          ...defaultState
        };
      }
    case AUTH_SET_AUTHENTICATED:
      if (state.user && state.token) {
        return {
          ...state,
          status: 'authenticated',
          msg: ''
        };
      }
      return state;
    case AUTH_SIGN_OUT:
      return {
        ...defaultState
      };
    default:
      return state;
  }
}



