import React from 'react';
import PropTypes from 'prop-types';
import SecondaryButton from '../forms/buttons/secondaryButton';

const twitterCredentialsReadOnly = ({twitterCredentialModel, onEdit}) => {
  return (
    <div className="twitterCredential__readOnly">
      <div className="twitterCredential__form--container">
        <div className="twitterCredential__form__readField">
          <span className="twitterCredential__form__readField--key">Consumer Key</span>
          <span className="twitterCredential__form__readField--val">{twitterCredentialModel.consumerKey || 'N/A'}</span>
        </div>
        <div className="twitterCredential__form__readField">
          <span className="twitterCredential__form__readField--key">Consumer Secret</span>
          <span className="twitterCredential__form__readField--val">{twitterCredentialModel.consumerSecret || 'N/A'}</span>
        </div>
        <div className="twitterCredential__form__readField">
          <span className="twitterCredential__form__readField--key">Access Token</span>
          <span className="twitterCredential__form__readField--val">{twitterCredentialModel.accessTokenKey || 'N/A'}</span>
        </div>
        <div className="twitterCredential__form__readField">
          <span className="twitterCredential__form__readField--key">Access Token Secret</span>
          <span className="twitterCredential__form__readField--val">{twitterCredentialModel.accessTokenSecret || 'N/A'}</span>
        </div>
      </div>
      
      <div className="twitterCredential__readOnly__actions">
        <div className="twitterCredential__actions">
          <SecondaryButton text="Edit Keys" clickHandler={onEdit}/>
        </div>
      </div>
    </div>
  );
};

twitterCredentialsReadOnly.propTypes = {
  twitterCredentialModel: PropTypes.object.isRequired,
  onEdit: PropTypes.func.isRequired
};

export default twitterCredentialsReadOnly;



