import React, { Component } from 'react';
import PropTypes from 'prop-types';
import TwitterIcon from '../../icons/twitterIcon.js';
import TwitterCredentialsReadOnly from './twitterCredentialsReadOnly';
import TwitterCredentialsForm from './twitterCredentialsForm';
import './twitterCredentials.css';

class TwitterCredentials extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isEditing: false
    };
  }
  
  renderForm() {
    return (
      <TwitterCredentialsForm
        twitterCredentialModel={this.props.model}
        onCancel={this.toggleIsEditing.bind(this)}
        onSave={this.saveTwitterCredential.bind(this)}
        />
    );
  }
  
  renderReadOnly() {
    return (
      <TwitterCredentialsReadOnly
        twitterCredentialModel={this.props.model}
        onEdit={this.toggleIsEditing.bind(this)} />
    );
  }
  
  toggleIsEditing() {
    this.setState(prevState => {
      return {
        isEditing: !prevState.isEditing
      };
    });
  }
  
  saveTwitterCredential(twitterCredentialModel) {
    if (!twitterCredentialModel) {
      return;
    }
    this.props.saveHandler(twitterCredentialModel);
    this.toggleIsEditing();
  }
  
  render() {
    return (
      <div className="twitterCredential">
        <div className="twitterCredential__header">
          <div className="twitterCredential__header__icon">
            <TwitterIcon />
          </div>
          <span className="twitterCredential__header__title">
            Twitter Keys
          </span>
        </div>
        
        {this.state.isEditing ? this.renderForm() : this.renderReadOnly()}
      </div>
    );
  }
}

TwitterCredentials.propTypes = {
  model: PropTypes.object.isRequired,
  saveHandler: PropTypes.func.isRequired
};

export default TwitterCredentials;



