import React from 'react';
import InnosolLogo from '../../components/innosolLogo/innosolLogo';
import './sideBar.css';

const sideBar = ({children}) => {
  return (
    <div className='sideBar'>
      <div className="sideBar__header">
        <div className="sideBar__header__icon">
          <InnosolLogo cssClass='sideBar__logo__icon' cssDecorationClass='sideBar__logo__decoration' cssTextClass="sideBar__logo__font"/>
        </div>
      </div>
      {children}
    </div>
  );
};

export default sideBar;



