import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { requestTeamList, resetTeams } from '../../store/actions/teams';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import { withRouter } from 'react-router-dom';
import TeamCard from './teamCard';
import './adminTeamList.css';

class AdminTeams extends Component {
  componentWillMount() {
    this.props.fetchTeams();
  }
  
  componentWillUnmount() {
    this.props.cleanupTeams();
  }

  render() {
    const buildTeamCard = team => {
      return (
        <TeamCard
          key={ team.id }
          team={team}
        />
      );
    };
    
    const renderTeamCards = () => {
      return (
        <CSSTransitionGroup
          transitionName="flyIn"
          transitionAppear={true}
          transitionAppearTimeout={0}
          transitionEnter={false}
          transitionLeave={false}
          >{this.props.teamList.map(cur => buildTeamCard(cur))}
        </CSSTransitionGroup>
      );
    };
    
    return (
      <div className="admin__team__list">
        {renderTeamCards()}
      </div>
    );
  }
}

AdminTeams.propTypes = {
  teams: PropTypes.object
};

const mapStateToProps = state => {
  return {
    teamList: state.teams.list
  };
};

const mapDispatchToProps = dispatch => {
  return {
    fetchTeams: () => dispatch(requestTeamList()),
    cleanupTeams: () => dispatch(resetTeams())
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AdminTeams));



