/**
 * Model States
 */
const modelStates = {
  dirty: 'dirty', // Model has changes that haven't been persisted to server
  persisting: 'persisting', // Model is dirty, and persiting changes to server
  pristine: 'pristine', // Model is in sync with the server
  new: 'new' // Model is brand new and has not been yet saved to server,
};

const defaultTwitterCredentialModel = {
  consumerKey: '',
  consumerSecret: '',
  accessTokenKey: '',
  accessTokenSecret: '',
  teamId: undefined,
  type: 'twitterCredential'
};

const makeNewTwitterCredentialModel = props => {
  return {
    ...defaultTwitterCredentialModel,
    ...props,
    state: modelStates.new
  };
};

function makeTwitterCredentialModel(props) {
  if (!props) {
    return;
  }
  const { consumerKey, consumerSecret, accessTokenKey, accessTokenSecret, teamId } = props;
  const modelProps =  {
    consumerKey,
    consumerSecret,
    accessTokenKey,
    accessTokenSecret,
    teamId
  };
  return {
    ...defaultTwitterCredentialModel,
    ...modelProps,
    state: modelStates.pristine
  };
}

export {
  makeNewTwitterCredentialModel,
  makeTwitterCredentialModel
};



