const USER_SESSION_KEY = 'INNOSOL_DOM_USER';

const getUserSessionFromStorage = () => JSON.parse(sessionStorage.getItem(USER_SESSION_KEY));
const persistUserSessionToStorage = userSession => sessionStorage.setItem(USER_SESSION_KEY, JSON.stringify(userSession));
const removeUserSessionFromStorage = () => sessionStorage.removeItem(USER_SESSION_KEY);

export {
  getUserSessionFromStorage,
  persistUserSessionToStorage,
  removeUserSessionFromStorage,
};



