const arrayHasDuplicates = (arr=[]) => {
  return (new Set(arr).size !== arr.length);
};

const arrayOfStrings = (arr=[]) => {
  return !arr.find(cur => typeof cur !== 'string');
};

export const uniqueStringArray = (props, propName, componentName) => {
  if (!Array.isArray(props[propName])) {
    return new Error(`Invalid prop \`${propName}\` of type \`${typeof(props[propName])}\` supplied to \`${componentName}\`, expected \`unique array\` of \`strings\``);
  }
  
  if (!arrayOfStrings(props[propName])) {
    return new Error(`Invalid prop \`${propName}\` of type \`${typeof(props[propName])}\` supplied to \`${componentName}\`, expected \`unique array\` of \`strings\``);
  }
  
  if (arrayHasDuplicates(props[propName])) {
    return new Error(`Invalid prop \`${propName}\` of type \`${typeof(props[propName])}\` supplied to \`${componentName}\`, expected \`unique array\` of \`strings\``);
  }
  
  return null;
};

export const arrayOf2Numbers = (props, propName, componentName) => {
  if (!Array.isArray(props[propName]) || props[propName].length !== 2 || !props[propName].every(Number.isInteger)) {
    return new Error(`Invalid prop \`${propName}\` of type \`${typeof(props[propName])}\` supplied to \`${componentName}\`, expected \`array\` of \`numbers\` with a \`length\` of 2`);
  }
  return null;
};



