import './styles/index.css';
import React from 'react';
import ReactDOM from 'react-dom';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import createStore from './store/createStore';
import SignIn from './containers/signIn/signIn';
import App from './containers/app/app';
import UnauthorizedRoute from './common/unauthorizedRoute';
import AuthorizedRoute from './common/authorizedRoute';
import { hydrateUserSession } from './store/actions/auth';
import './polyfills/requestIdleCallback';

// ======================================================
// Register Service Worker
// ======================================================
registerServiceWorker();

// ======================================================
// Create Redux Store
// ======================================================
const store = createStore();

// ======================================================
// Hydrate the application
// ======================================================
store.dispatch(hydrateUserSession());

// ======================================================
// Root Component
// ======================================================

const Root = () => {
  return (
    <BrowserRouter>
      <div className="router">
        <UnauthorizedRoute path="/signin" component={SignIn} />
        <AuthorizedRoute component={App} />
      </div>
    </BrowserRouter>
  );
};

// ======================================================
// Render Root To DOM
// ======================================================

ReactDOM.render(
  <Provider store={store}>
    <Root />
  </Provider>,
  document.getElementById('root')
);



