import React from 'react';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import ClassNames from 'classnames';
import CheckIcon from '../../../icons/checkIcon';
import InfiniteSpinner from '../../spinners/infiniteSpinner';
import ButtonRipple from './buttonRipple';
import PropTypes from 'prop-types';
import './primaryButton.css';

const primaryButton = ({status, clickHandler, text}) => {
  const getBtnClassname = () => {
    return ClassNames(
      'primaryButton',
      {
        'primaryButton--loading': status === 'loading',
        'primaryButton--success': status === 'success',
        'primaryButton--error': status === 'error'
      },
    );
  };
  
  const renderCheckmark = () => {
    return (
      <CSSTransitionGroup
        transitionName="fadeIn"
        transitionAppear={true}
        transitionAppearTimeout={200}
        transitionEnter={false}
        transitionLeave={false}>
        <div className="primaryButton--checkMark">
          <CheckIcon />
        </div>
      </CSSTransitionGroup>
    );
  };
  
  const renderSpinner = () => {
    return (
      <div className="primaryButton--spinner">
        <InfiniteSpinner />
      </div>
    );
  };
  
  const renderRipple = () => {
    return ( <ButtonRipple /> );
  };
  
  return (
    <button
      className={getBtnClassname()}
      onClick={() => clickHandler()}
      >
      <span>{text}</span>
      {status === 'success' && renderCheckmark()}
      {status === 'loading' && renderSpinner()}
      {renderRipple()}
    </button>
  );
};

primaryButton.propTypes = {
  status: PropTypes.string,
  clickHandler: PropTypes.func,
  text: PropTypes.string
};

export default primaryButton;



