import React from 'react';
import './signInInput.css';

const signInInput = ({children, inputType, changeHandler}) => {
  return (
    <div className='signIn__input'>
      <div className="signIn__input__container">
        <div className="signIn__input__icon">
          {children}
        </div>
        <input
          className="signIn__input--input"
          type={inputType}
          onChange={changeHandler}
        />
      </div>
    </div>
  );
};

signInInput.propTypes = {
  
};

export default signInInput;



