import React from 'react';
import PropTypes from 'prop-types';
import MoreVertIcon from '../../icons/moreVertIcon';
import './appBarSettings.css';

const appBarSettings = ({userName, toggleSettingsMenu}) => {
  return (
    <div className="appBar__settings">
      <span className="appBar__settings__userName">
        {userName}
      </span>
      
      <div className="appBar__settings__more">
        <div className="appBar__settings__button" onClick={toggleSettingsMenu}>
          <MoreVertIcon />
        </div>
      </div>
    </div>
  );
};

appBarSettings.propTypes = {
  userName: PropTypes.string
};

export default appBarSettings;



