import moment from 'moment';

const defaultStatus = 'pending';
const defaultStatusMsg = 'Waiting for Integration to start.';

const baseModel = {
  defaultStatus,
  defaultStatusMsg,
  type: 'twitter'
};

function makeNewIntegrationModel() {
  const createdDate = new Date();
  return {
    ...baseModel,
    createdDate,
    formattedCreatedDate: moment(createdDate).format('MMMM Do YYYY, h:mm a'),
    statusMsg: 'Waiting for integration to start.',
    status: 'pending',
    type: 'twitter',
    id: -1
  };
}

function makeIntegrationModel(props) {
  if (!props) {
    return;
  }
  const {
    id,
    createdDate,
    finishedDate,
    teamId,
    completedCount,
    totalCount,
    userInProgress,
    createdByName,
    createdById,
    type
  } = props;
  
  const status = props.status || defaultStatus;
  const statusMsg = props.statusMsg || defaultStatusMsg;
  const formattedCreatedDate = createdDate ? moment(new Date(createdDate)).format('MMMM Do YYYY, h:mm a') : undefined;
  const formattedFinishedDate = finishedDate ? moment(new Date(finishedDate)).format('MMMM Do YYYY, h:mm a') : undefined;
  
  const data = {
    type,
    createdDate,
    formattedCreatedDate,
    finishedDate,
    formattedFinishedDate,
    teamId,
    completedCount,
    totalCount,
    status,
    statusMsg,
    userInProgress,
    createdByName,
    createdById,
    id
  };
  
  return {
    ...baseModel,
    ...data
  };
}

export {
  makeIntegrationModel,
  makeNewIntegrationModel
};



