import callApi from '../api/call';
import { makeApiError } from '../api/makeApiError';

function authenticateUser(username, password) {
  const params = {
    body: { username, password }
  };
  
  return callApi('signin', 'post', params)
    .then(resp => {
      if (resp.success) {
        return resp;
      }
      throw makeApiError(400, 'Incorrect username or password.');
    })
    .catch(error => ({error}));
}

function isUserSessionActive(userSession = {}) {
  if (!userSession) {
    return false;
  }
  const { tokenExpirationDate } = userSession;
  if (!tokenExpirationDate) {
    return false;
  }
  // If the expiration UTC is within 1 minute of expiration simply return false and have the
  // User sign in again.
  return (new Date(tokenExpirationDate).getTime() < (new Date().getTime() + 60000)) ? false : true;
}

export {
  authenticateUser,
  isUserSessionActive
};



