import { makeActionCreator } from './index';

export const TEAM_PROFILE_REQUEST = 'TEAM_PROFILE_REQUEST';
export const TEAM_PROFILE_RECEIVE = 'TEAM_PROFILE_RECEIVE';
export const TEAM_PROFILE_RESET = 'TEAM_PROFILE_RESET';
export const TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_RECEIVE = 'TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_RECEIVE';
export const TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST = 'TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST';
export const TEAM_PROFILE_NEW_INTEGRATION_REQUEST = 'TEAM_PROFILE_NEW_INTEGRATION_REQUEST';
export const TEAM_PROFILE_NEW_INTEGRATION_RECEIVE = 'TEAM_PROFILE_NEW_INTEGRATION_RECEIVE';
export const TEAM_PROFILE_REQUEST_DELETE_INTEGRATION = 'TEAM_PROFILE_REQUEST_DELETE_INTEGRATION';
export const TEAM_PROFILE_REQUEST_INTEGRATION_REPORT = 'TEAM_PROFILE_REQUEST_INTEGRATION_REPORT';

export const requestTeamProfile = makeActionCreator(TEAM_PROFILE_REQUEST, 'name');
export const receiveTeamProfile = makeActionCreator(TEAM_PROFILE_RECEIVE, 'teamProfile');
export const resetTeamProfile = makeActionCreator(TEAM_PROFILE_RESET);
export const requestSaveTwitterCredential = makeActionCreator(TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST, 'twitterCredentialModel');
export const receiveSaveTwitterCredential = makeActionCreator(TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_RECEIVE, 'twitterCredentialModel');
export const requestStartIntegration = makeActionCreator(TEAM_PROFILE_NEW_INTEGRATION_REQUEST, 'teamId', 'integrationType');
export const receiveStartIntegration = makeActionCreator(TEAM_PROFILE_NEW_INTEGRATION_RECEIVE, 'integrationModel');
export const requestDeleteIntegration = makeActionCreator(TEAM_PROFILE_REQUEST_DELETE_INTEGRATION, 'id');
export const requestIntegrationReport = makeActionCreator(TEAM_PROFILE_REQUEST_INTEGRATION_REPORT, 'id');



