import React from 'react';
import './infiniteSpinner.css';

const loadingSpinner = () => {
  return (
    <svg className="spinner" height="65px" width="65px" viewBox="0 0 66 66" xmlns="http://www.w3.org/2000/svg">
      <circle className="infiniteSpinner__path" fill="none" strokeWidth="6" strokeLinecap="round" cx="33" cy="33" r="30"></circle>
    </svg>
  );
};

export default loadingSpinner;



