import React, {Component } from 'react';
import PropTypes from 'prop-types';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import ExpandMore from '../../icons/expandMoreIcon';
import classNames from 'classnames';
import { uniqueStringArray } from '../../library/propTypes';
import './filterDropdown.css';

class FilterDropdown extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFocused: false
    };
  };
  
  focusInputHandler(e) {
    e.preventDefault();
    e.stopPropagation();
    if (!this.inputRef) {
      return;
    }
    return (this.inputRef === document.activeElement) ? this.inputRef.blur() : this.inputRef.focus();
  }
  
  onFocus() {
    this.setState(() => ({isFocused: true}));
  }
  
  onBlur() {
    this.setState(() => ({isFocused: false}));
  }
  
  getInputClassname() {
    return classNames(
      'filterDropdown__input__control',
      {'filterDropdown__input__control--focused': this.state.isFocused},
      {'filterDropdown__input__control--disabled': this.props.isDisabled}
    );
  }
  
  dropdownMouseHandler(val) {
    this.props.selectHandler(val);
    this.onBlur();
  }
  
  renderDropdownItems() {
    return this.props.items.map(cur => {
      return (
        <div key={cur} className="filterDropdown__dropdown__item" onMouseDown={() => this.dropdownMouseHandler(cur)}>
          <span data-val={cur} className="filterDropdown__dropdown__item--text"> {cur} </span>
        </div>
      );
    });
  }
  
  renderDropdown() {
    return (
      <CSSTransitionGroup
        transitionName="flyIn"
        transitionAppear={false}
        transitionEnterTimeout={200}
        transitionEnter={true}
        transitionLeave={true}
        transitionLeaveTimeout={200}
        >
        {
          this.state.isFocused && (
            <div className="filterDropdown__dropdown">
              {this.renderDropdownItems()}
            </div>
          )
        }
      </CSSTransitionGroup>
    );
  }
    
  render() {
    return (
      <div className="filterDropdown">
        <div className={this.getInputClassname()} onMouseDown={e => this.focusInputHandler(e)}>
          <input
            ref={el => this.inputRef = el}
            type="text"
            noValidate
            className="filterDropdown__input"
            name="filterDropdown__input"
            onFocus={() => this.onFocus()}
            onBlur={() => this.onBlur()}
            />
          <span className="filterDropdown__input__value">{this.props.selectedItem || this.props.items[0]}</span>
          <div className="filterDropdown__input__dropdownArrow"><ExpandMore /></div>
        </div>
        { this.renderDropdown() }
      </div>
    );
  }
}

FilterDropdown.propTypes = {
  selectedItem: PropTypes.string,
  items: uniqueStringArray,
  selectHandler: PropTypes.func.isRequired,
  isDisabled: PropTypes.bool
};

FilterDropdown.defaultProps = {
  items: []
};

export default FilterDropdown;



