import { call, put, takeLatest } from 'redux-saga/effects';
import {
  TEAM_PROFILE_REQUEST,
  receiveTeamProfile,
  TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST,
  receiveSaveTwitterCredential,
  TEAM_PROFILE_NEW_INTEGRATION_REQUEST,
  receiveStartIntegration,
  TEAM_PROFILE_REQUEST_DELETE_INTEGRATION,
  TEAM_PROFILE_REQUEST_INTEGRATION_REPORT
} from '../store/actions/teamProfile';

import {
  getTeamProfileDetails,
  saveTwitterCredential,
  createIntegration,
  deleteIntegration,
  downloadIntegrationReports
} from '../services/teamService';

function *fetchTeamProfile(action={}) {
  const resp = yield call(getTeamProfileDetails, action.name);
  if (resp.error) {
    // TODO: Handle error case
    return;
  }
  yield(put(receiveTeamProfile(resp.results || {})));
}

function *persistTwitterCredential(action={}) {
  const resp = yield call(saveTwitterCredential, action.twitterCredentialModel);
  if (resp.error) {
    // TODO: Handle error case
    return;
  }
  yield(put(receiveSaveTwitterCredential(resp.results || {})));
}

function *persistIntegration(action={}) {
  const resp = yield call(createIntegration, action.teamId, action.integrationType);
  if (resp.error) {
    // TODO: Handle error case
    return;
  }
  yield(put(receiveStartIntegration(resp.results || {})));
}

function *removeIntegration(action={}) {
  yield call(deleteIntegration, action.id);
}

function *downloadReportsForIntegration(action={}) {
  yield call(downloadIntegrationReports, action.id);
}

function *teamProfileSaga() {
  yield takeLatest(TEAM_PROFILE_REQUEST, fetchTeamProfile);
  yield takeLatest(TEAM_PROFILE_TWITTER_CREDENTIAL_SAVE_REQUEST, persistTwitterCredential);
  yield takeLatest(TEAM_PROFILE_NEW_INTEGRATION_REQUEST, persistIntegration);
  yield takeLatest(TEAM_PROFILE_REQUEST_DELETE_INTEGRATION, removeIntegration);
  yield takeLatest(TEAM_PROFILE_REQUEST_INTEGRATION_REPORT, downloadReportsForIntegration);
}

export default teamProfileSaga;



