import React, { Component } from 'react';
import DashboardIcon from '../../icons/dashboardIcon';
import PowerIcon from '../../icons/powerIcon';
import { Link } from 'react-router-dom';
import './appBarSettingsMenu.css';

class AppBarSettingsMenu extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.onMouseClick.bind(this);
  }

  onMouseClick(e) {
    if (this.menuRef && (this.menuRef.isSameNode(e.target) || this.menuRef.contains(e.target))) {
      return;
    }
    this.props.closeHandler();
  }

  componentDidMount() {
    document.addEventListener('click', this.handleClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleClick);
  }

  setMenuRef(el) {
    if (!el) return;
    this.menuRef = el;
  }

  renderAdminItem() {
    return (
      <Link to="/admin" className="appBar__settings__menu--row" onClick={() => this.props.closeHandler()}>
        <DashboardIcon />
        Admin
      </Link>
    );
  }
  
  renderSignOutItem() {
    return (
      <Link to="/signout" className="appBar__settings__menu--row" onClick={() => this.props.closeHandler()}>
        <PowerIcon />
          Signout
      </Link>
    );
  };

  render() {
    return (
      <div className="appBar__settings__menu__container">
        <div className="appBar__settings__menu" ref={el => this.setMenuRef(el)}>
          {this.renderAdminItem()}
          {this.renderSignOutItem()}
        </div>
      </div>
    );
  }
}

export default AppBarSettingsMenu;



