import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import UserIcon from '../../icons/userIcon';
import LockIcon from '../../icons/lockIcon';
import SignInButton from './signInButton';
import SignInLogo from './signInLogo';
import SignInInput from './signInInput';
import { signInRequest, setAuthenticated } from '../../store/actions/auth';
import './signIn.css';

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: ''
    };
    
    const onKeydown = e => {
      if (e.keyCode !== 13) {
        return;
      }
      this.handleSignIn();
    };
    this.keyPressEventHandler = onKeydown.bind(this);
  }
  
  componentDidMount() {
    document.addEventListener('keydown', this.keyPressEventHandler);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.keyPressEventHandler);
  }
  
  componentWillReceiveProps(newProps) {
    if (newProps.user && !this.props.user) {
      this.handleSignInSuccess();
    }
  }
  
  handleSignIn() {
    if (!this.state.username || !this.state.password || this.state.status === 'submitting') {
      return;
    }
    return this.props.signIn(this.state.username, this.state.password);
  }
  
  handleSignInSuccess() {
    // Redirect after 1 second
    setTimeout(() => {
      this.props.setUserAuthenticated();
    }, 1000);
  }
  
  renderErrorMsg() {
    return (
      <CSSTransitionGroup
        transitionName="fadeIn"
        transitionAppear={false}
        transitionEnterTimeout={200}
        transitionEnter={true}
        transitionLeave={false}
        >
        <div className="signIn__form__actions__msg">
          {this.props.status === 'error' && this.props.msg}
        </div>
      </CSSTransitionGroup>
    );
  }
  
  render() {
    const { from } = this.props.location.state || { from: { pathname: '/' }};
    if (this.props.isAuthenticated) {
      return ( <Redirect to={from} /> );
    }
    
    const getBtnStatus = () => {
      if (this.props.status === 'error') {
        return 'error';
      }
      if (this.props.user) {
        return 'success';
      }
      if (this.props.status === 'authenticating') {
        return 'loading';
      }
    };
    
    return (
      <div className="signIn">
        <div className="signIn__container">
          <div className="signIn__logo__container">
            <SignInLogo />
          </div>
          <CSSTransitionGroup
            transitionName="signInFormEnter"
            transitionAppear={true}
            transitionAppearTimeout={330}
            transitionEnter={false}
            transitionLeave={false}
            >
          
          <div className="signIn__form">
            <div className="signIn__form__body">
              
              <div className="signIn__form__body__inputs">
                <SignInInput
                  inputType="text"
                  changeHandler={e => this.setState({username: e.target.value})}
                  >
                  <UserIcon />
                </SignInInput>
                <SignInInput
                  inputType="password"
                  changeHandler={e => this.setState({password: e.target.value})}
                  >
                  <LockIcon />
                </SignInInput>
              </div>
            </div>
            
            <div className="signIn__form__actions">
              <div className="signIn__form__actions--btn">
                <SignInButton
                  status={getBtnStatus()}
                  clickHandler={() => this.handleSignIn()}
                  text="Sign In" />
              </div>
              {this.renderErrorMsg()}
            </div>
          </div>
          </CSSTransitionGroup>
        </div>
      </div>
    );
  }
}

SignIn.propTypes = {
  user: PropTypes.object,
  status: PropTypes.string,
  msg: PropTypes.string
};

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    isAuthenticated: state.auth.status === 'authenticated',
    status: state.auth.status,
    msg: state.auth.msg
  };
};

const mapDispatchToProps = dispatch => {
  return {
    signIn: (username, password) => dispatch(signInRequest(username, password)),
    setUserAuthenticated: () => dispatch(setAuthenticated())
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SignIn));



