import { applyMiddleware, compose, createStore as createReduxStore } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { makeRootReducer } from './reducers/index';
import initialState from './initialState';
import authSaga from '../sagas/authSaga';
import teamsSaga from '../sagas/teamsSaga';
import teamProfileSaga from '../sagas/teamProfileSaga';
import userSession from './middleware/userSession';
import apiErrorHandler from './middleware/apiErrorHandler';

const createStore = () => {
  // ======================================================
  // Middleware Configuration
  // ======================================================
  const sagaMiddleware = createSagaMiddleware();
  const middleware = [sagaMiddleware, userSession, apiErrorHandler];
  
  // ======================================================
  // Store Enhancers
  // ======================================================
  const enhancers = [];
  let composeEnhancers = compose;
  
  if (process.env.NODE_ENV !== 'production') {
    if (window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) {
      composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;
    }
  }
  
  // ======================================================
  // Store Instantiation
  // ======================================================
  const store = createReduxStore(
    makeRootReducer(),
    initialState,
    composeEnhancers(
      applyMiddleware(...middleware),
      ...enhancers
    )
  );
  
  
  // ======================================================
  // Run Sagas
  // ======================================================
  sagaMiddleware.run(authSaga);
  sagaMiddleware.run(teamsSaga);
  sagaMiddleware.run(teamProfileSaga);
  return store;
};

export default createStore;



