import React from 'react';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import ClassNames from 'classnames';
import CheckIcon from '../../icons/checkIcon';
import InfiniteSpinner from '../../components/spinners/infiniteSpinner';
import ButtonRipple from '../../components/forms/buttons/buttonRipple';
import PropTypes from 'prop-types';
import './signInButton.css';

const signInButton = ({status, clickHandler, text}) => {
  const getBtnClassname = () => {
    return ClassNames(
      'signInButton',
      {
        'signInButton--loading': status === 'loading',
        'signInButton--success': status === 'success',
        'signInButton--error': status === 'error'
      },
    );
  };
  
  const renderCheckmark = () => {
    return (
      <CSSTransitionGroup
        transitionName="fadeIn"
        transitionAppear={true}
        transitionAppearTimeout={200}
        transitionEnter={false}
        transitionLeave={false}>
        <div className="signInButton--checkMark">
          <CheckIcon />
        </div>
      </CSSTransitionGroup>
    );
  };
  
  const renderSpinner = () => {
    return (
      <div className="signInButton--spinner">
        <InfiniteSpinner />
      </div>
    );
  };
  
  const renderRipple = () => {
    return ( <ButtonRipple /> );
  };
  
  return (
    <button
      className={getBtnClassname()}
      onClick={() => clickHandler()}
      >
      <span>{text}</span>
      {status === 'success' && renderCheckmark()}
      {status === 'loading' && renderSpinner()}
      {renderRipple()}
    </button>
  );
};

signInButton.propTypes = {
  status: PropTypes.string,
  clickHandler: PropTypes.func,
  text: PropTypes.string
};

export default signInButton;



