import { call, put, takeLatest } from 'redux-saga/effects';
import { TEAMS_REQUEST, receiveTeamList } from '../store/actions/teams';
import { getTeamList } from '../services/teamService';

function *requestTeamList(action={}) {
  const teamsResp = yield call(getTeamList);
  if (teamsResp.error) {
    // TODO: Handle error case
    return;
  }
  yield(put(receiveTeamList(teamsResp.results)));
}

function *teamsSaga() {
  yield takeLatest(TEAMS_REQUEST, requestTeamList);
}

export default teamsSaga;



