import {
  AUTH_HYDRATE_USER_SESSION,
  AUTH_SIGN_IN_RECEIVE,
  AUTH_SIGN_OUT
} from '../actions/auth';

import { isUserSessionActive } from '../../services/authService';

import {
  getUserSessionFromStorage,
  persistUserSessionToStorage,
  removeUserSessionFromStorage
} from '../../services/storageService';

function userSessionMiddleware(state) {
  return dispatch => next => action => {
    if (action.type === AUTH_HYDRATE_USER_SESSION) {
      // Attempt to hydrate user session data from storage
      const userSession = getUserSessionFromStorage();
      if (isUserSessionActive(userSession)) {
        action.sessionData = userSession;
      } else {
        // Remove session for good measure
        removeUserSessionFromStorage();
      }
    } else if (action.type === AUTH_SIGN_IN_RECEIVE) {
      persistUserSessionToStorage(action.userSession);
    } else if (action.type === AUTH_SIGN_OUT) {
      removeUserSessionFromStorage();
    }
    return next(action);
  };
}

export default userSessionMiddleware();



