import { makeActionCreator } from './index';

export const TEAMS_RESET = 'TEAMS_RESET';
export const TEAMS_RECEIVE = 'TEAMS_RECEIVE';
export const TEAMS_REQUEST = 'TEAMS_REQUEST';

export const requestTeamList = makeActionCreator(TEAMS_REQUEST);
export const receiveTeamList = makeActionCreator(TEAMS_RECEIVE, 'teams');
export const resetTeams = makeActionCreator(TEAMS_RESET);



